.privacy-page {
	padding-top: 260px;
}
.privacy-page .title {
	font-weight: 700;
	font-size: 40px;
	line-height: 47.25px;
	text-align: start;
	text-transform: uppercase;
	margin-bottom: 34px;
}
.privacy-page .description {
	font-weight: 400;
	font-size: 20px;
	line-height: 32px;
	color: #716f6f;
	width: 75%;
}
@media screen and (max-width: 500px) {
	.privacy-page {
		margin-top: 32px;
	}
	.privacy-page .title {
		font-size: 24px;
		margin-bottom: 18px;
	}
	.privacy-page .description {
		font-size: 18px;
		line-height: 20px;
	}
}
