.categories {
	margin-top: 64px;
	margin-bottom: 64px;
}
.categories .title {
	font-weight: 700;
	font-size: 47.67px;
	line-height: 100%;
	letter-spacing: 0%;
	margin-bottom: 40px;
}
.categories .category-card {
	text-align: center;
}
.categories .category-card p {
	font-weight: 700;
	font-size: 32px;
	line-height: 43.26px;
	text-align: center;
	margin-top: 18px;
}
.categories .category-card-image {
	background-color: #fcf2e9b2;
	box-shadow: 0px 0px 24.72px 0px #b4d7d833;
	border-radius: 15px;
	text-align: center;
	padding: 40px 60px;
}
.categories .category-card-image img {
	width: 170px;
	height: 170px;
}
@media screen and (max-width: 900px) {
	.categories .category-card-image {
		padding: 18px;
	}
	.categories .category-card-image img {
		width: 100px;
		height: 100px;
	}
	.categories .category-card p {
		font-size: 18px;
	}
}

@media screen and (max-width: 500px) {
	.categories {
		margin-top: 40px;
		margin-bottom: 40px;
	}
	.categories .title {
		font-size: 32px;
	}
	.categories .category-card-image {
		padding: 18px;
	}
	.categories .category-card-image img {
		width: 100px;
		height: 100px;
	}
	.categories .category-card p {
		font-size: 18px;
	}
}
