nav {
	width: 90%;
	position: absolute;
	top: 32px;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: 9;
}
.navbar {
	height: 100px;
	position: relative;
	box-shadow: 0px 0px 12px 0px #00000026;
	background: #fff;
	padding: 12px 60px;
	border-radius: 100px;
	display: flex;
	justify-content: space-between;
}
.navbar .nav-logo {
	height: 75px;
}
.navbar .nav-logo img {
	height: 100%;
}
.navbar .navbar-expand-lg .navbar-nav {
	display: flex;
	gap: 25px;
}
.navbar .navbar-expand-lg .navbar-nav .download.tablet.mobile,
.navbar .navbar-expand-lg .navbar-nav .nav-link {
	font-weight: 300;
	font-size: 24px;
	line-height: 100%;
	text-transform: capitalize;
	text-decoration: none;
	color: #000;
}
.navbar .navbar-expand-lg .navbar-nav .nav-link.active {
	color: var(--primary-color);
}
.navbar .web.download {
	background: var(--primary-color);
	color: #fff;
	border-radius: 5px;
	height: 50px;
	font-weight: 700;
	line-height: 2;
	padding: 10px 15px;
	text-transform: uppercase;
	text-decoration: none;
}
.web {
	display: block !important;
}
.mobile,
.tablet {
	display: none !important;
}
@media screen and (max-width: 900px) {
	.web {
		display: none !important;
	}
	.tablet {
		display: block !important;
	}
	.navbar {
		display: flex;
		justify-content: space-between;
		padding: 7px 32px;
		transition: all 0.25s ease-in-out;
	}
	/* .navbar:has(.show) {
		border-radius: 5% 5% 0 0;
	} */
	.navbar .navbar-collapse {
		position: absolute;
		width: 100%;
		left: 0;
		top: 100px;
		background: #fff;
		padding: 2rem;
		box-shadow: 0px 0px 12px 0px #00000026;
		border-radius: 25px;
	}
	.navbar-toggler:active,
	.navbar-toggler:focus {
		box-shadow: none;
		border: none;
	}
	.navbar .hamburger {
		position: relative;
		width: 30px;
		height: 22px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		cursor: pointer;
		background: none;
		border: none;
		padding: 0;
		transition: transform 0.3s ease;
	}
	.navbar .hamburger span {
		display: block;
		height: 3px;
		width: 100%;
		background: #000;
		border-radius: 2px;
		transition: all 0.6s ease;
	}
	/* Animation when menu is open */
	.navbar .hamburger.open span:nth-child(1) {
		transform: translateY(9px) rotate(45deg);
	}

	.navbar .hamburger.open span:nth-child(2) {
		opacity: 0;
	}

	.navbar .hamburger.open span:nth-child(3) {
		transform: translateY(-10px) rotate(-45deg);
	}
	.navbar .navbar-collapse .navbar-nav {
		width: 100%;
		display: flex;
	}
	.navbar .navbar-collapse .navbar-nav div {
		display: flex;
		flex-direction: column;
		width: 100%;
		align-items: start;
	}
	.navbar .nav-logo img {
		height: 100%;
	}
	.navbar .navbar-expand-lg {
		width: auto;
	}
	.navbar .download {
		color: #000;
		background: none;
		padding: 10px 0;
	}
}
@media screen and (max-width: 500px) {
	.mobile {
		display: block !important;
	}
	nav {
		top: 20px;
	}
	nav .topnavbar .email-phone {
		flex-direction: column;
		gap: 8px;
		align-items: start;
	}
	nav .topnavbar .email-phone span {
		font-size: 16px;
	}
	nav .topnavbar .email-phone div {
		gap: 8px;
	}
	nav .topnavbar .email-phone .phone {
		display: none;
	}
	nav .topnavbar .social {
		gap: 8px;
	}
}
