header {
	/* width: 90%; */
	margin: auto;
	position: relative;
	background: linear-gradient(360deg, #a6f6fc 0%, #558c90 100%);
	border: 1px solid #28b998;
	margin-top: 175px;
	border-radius: 20px;
	padding: 37px 32px;
}

.header .title-head .title {
	font-weight: 700;
	font-size: 60px;
	line-height: 130%;
	text-transform: uppercase;
	color: #fff;
	margin-bottom: 18px;
}
.header .title-head .description {
	color: #fff;
	font-size: 18px;
	font-weight: 400;
	line-height: 24px;
	margin-bottom: 50px;
}
.header .title-head .download-now {
	color: #232233;
	font-weight: 600;
	font-size: 25px;
	line-height: 37.5px;
	margin-bottom: 16px;
	text-transform: uppercase;
}
.header .title-head .download-apps {
	display: flex;
	gap: 14px;
}
.phone-head .phone {
	position: relative;
	width: 200px;
	height: 400px;
}
.phone-head .phone img {
	position: absolute;
}
.phone-head .phone .iphone {
	width: 100%;
	height: 100%;
	z-index: 2;
	top: 0;
	left: 0;
}
.phone-head .phone .screen {
	height: 96%;
	left: 10px;
	top: 8px;
	width: 90%;
	z-index: 1;
}
@media screen and (max-width: 900px) {
	.header {
		padding-top: 0;
		padding-bottom: 0;
	}
	.header {
		width: 100%;
		border-width: 12px;
	}
	.header .title-head {
		margin: 0;
		padding: 30px 20px;
	}
	.header .title-head .title {
		font-size: 32px;
		line-height: 100%;
	}
	.header .title-head .description {
		margin-bottom: 30px;
	}
	.header .title-head .download-now {
		font-size: 20px;
	}
	.header .title-head .download-apps {
		/* flex-direction: column; */
		gap: 8px;
	}
	.header .title-head .download-apps img {
		width: 50%;
	}
	.phone-head .phone .screen {
		left: 11px;
		top: 8px;
	}
}
@media screen and (max-width: 500px) {
	header {
		padding: 18px;
	}
	.header {
		padding-top: 0;
		padding-bottom: 0;
	}
	.header {
		width: 100%;
		border-width: 8px;
		margin-bottom: 1.5rem;
	}
	.header .title-head {
		margin: 0;
		padding: 20px 15px;
	}
	.header .title-head .title {
		font-size: 32px;
		line-height: 100%;
	}
	.header .title-head .description {
		line-height: 22px;
		margin-bottom: 30px;
	}
	.header .title-head .download-now {
		font-size: 22px;
		margin-bottom: 12px;
	}
	.header .justify-center-mobile {
		justify-content: center !important;
	}
	.phone-head .phone .screen {
		left: 11px;
		top: 8px;
	}
}
