/* SATOSHI REGULAR */
@font-face {
	font-family: "Satoshi";
	src: url("./fonts/Satoshi-Regular.otf") format("opentype");
	font-weight: 400;
	font-style: normal;
}

/* SATOSHI ITALIC */
@font-face {
	font-family: "Satoshi";
	src: url("./fonts/Satoshi-Italic.otf") format("opentype");
	font-weight: 400;
	font-style: italic;
}

