footer {
	background: linear-gradient(360deg, #80c9ce 0%, #558c90 100%);
	box-shadow: 0px 0px 12px 0px #00000026;
	padding: 48px 45px 16px;
	margin-top: 65px;
	margin-bottom: 40px;
	border-radius: 20px;
}
footer .social {
	display: flex;
	gap: 30px;
}
footer .social svg {
	fill: var(--primary-color);
}
footer .social .separte {
	height: 20px;
	width: 1px;
	background-color: #fff;
}
footer hr {
	color: #fff;
}
.gap-8 {
	gap: 8px;
}
footer .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	color: #fff;
	margin-top: 20px;
	margin-bottom: 22px;
	padding-right: 3rem;
}
footer .links a {
	margin-top: 12px;
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	color: #fff;
	text-decoration: none;
	display: block;
}
footer .title {
	font-weight: 600;
	font-size: 18px;
	line-height: 37.5px;
	text-transform: uppercase;
	color: #fff;
	margin-bottom: 20px;
}
footer .subscribe-input {
	background-color: #fff;
	border-radius: 10px;
	padding: 5px 8px;
	display: flex;
	align-items: center;
	gap: 8px;
}
footer .subscribe-input input {
	border: 0;
	outline: 0;
	width: 100%;
}
footer .power {
	font-weight: 400;
	font-size: 14px;
	line-height: 100%;
	color: #fff;
	text-align: center;
	margin: 0;
}
footer a {
	color: #fff;
	text-decoration: none;
}
@media screen and (max-width: 900px) {
	footer .social {
		gap: 12px;
	}
	footer .description {
		padding: 0;
	}
}
@media screen and (max-width: 500px) {
	footer {
		padding: 24px 0 16px;
		margin-top: 62px;
		text-align: center;
	}
	.justify-center-mobile {
		justify-content: center !important;
	}
	footer .col-md-6:first-of-type {
		text-align: center;
		margin-bottom: 1rem;
	}
	footer .col-md-4:first-of-type img {
		margin-bottom: 1rem;
	}
	footer .col-md-4:first-of-type .description {
		padding: 0;
	}
	footer .social {
		justify-content: center;
		gap: 12px;
	}
	footer .links {
		margin-bottom: 1.5rem;
	}
	footer .title {
		font-size: 18px;
		margin-bottom: 8px;
	}
	footer .links div {
		display: grid;
		grid-template-columns: auto;
		gap: 8px;
	}
	footer .links a {
		margin: 0;
	}
	footer .description {
		margin-top: 0;
	}
	.power-sec {
		display: flex;
		flex-direction: column;
	}
	footer img.d-block {
		margin: auto;
	}
}
